.book-detail-page {
  padding: 40px 20px;
  max-width: 1200px;
  margin: 0 auto;
  min-height: 80vh;
}

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60vh;
  font-size: 20px;
  color: var(--text-muted);
}

.back-button {
  background: none;
  border: none;
  padding: 10px 0;
  margin-bottom: 30px;
  font-size: 16px;
  color: var(--primary-color);
  cursor: pointer;
  display: flex;
  align-items: center;
  transition: all 0.3s ease;
}

.back-button:hover {
  color: var(--primary-dark);
  transform: translateX(-5px);
}

.book-detail-container {
  display: flex;
  gap: 50px;
  margin-bottom: 50px;
}

.book-detail-cover {
  flex: 0 0 350px;
  height: 500px;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
}

.book-detail-cover img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.book-cover-placeholder {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, var(--primary-light), var(--primary-color));
  color: white;
  font-size: 80px;
  font-weight: bold;
}

.book-detail-info {
  flex: 1;
}

.book-detail-genre {
  display: inline-block;
  background-color: var(--primary-light);
  color: var(--primary-dark);
  padding: 6px 15px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 15px;
}

.book-detail-title {
  font-size: 42px;
  margin: 0 0 10px;
  color: var(--text-dark);
  line-height: 1.2;
}

.book-detail-author {
  font-size: 24px;
  color: var(--text-muted);
  margin: 0 0 20px;
  font-weight: 500;
}

.book-detail-year {
  font-size: 16px;
  color: var(--text-light);
  margin-bottom: 30px;
}

.book-detail-description {
  margin-bottom: 30px;
}

.book-detail-description h3 {
  font-size: 20px;
  margin-bottom: 15px;
  color: var(--text-dark);
}

.book-detail-description p {
  font-size: 16px;
  line-height: 1.7;
  color: var(--text-dark);
}

.book-detail-actions {
  display: flex;
  gap: 10px;
  margin-top: 20px;
}

.action-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  font-weight: 500;
  padding: 12px 20px;
  border-radius: 8px;
  transition: all 0.3s ease;
  cursor: pointer;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.action-button svg {
  transition: all 0.3s ease;
}

.action-button:hover svg {
  transform: translateY(-2px);
}

.action-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
}

.action-button:active {
  transform: translateY(0);
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1);
}

.action-button.primary {
  background-color: var(--primary-color);
  color: white;
}

.action-button.primary:hover {
  background-color: var(--primary-dark);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(52, 152, 219, 0.3);
}

.action-button.secondary {
  background-color: white;
  color: var(--primary-color);
  border: 2px solid var(--primary-color);
}

.action-button.secondary:hover {
  background-color: var(--primary-light);
  transform: translateY(-2px);
}

.action-button.secondary.active {
  background-color: var(--primary-color);
  color: white;
  border-color: var(--primary-dark);
}

.action-button.secondary.active:hover {
  background-color: #c0392b;
  border-color: #c0392b;
}

.book-detail-sections {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  margin-top: 50px;
}

.book-detail-section {
  background-color: white;
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.book-detail-section h3 {
  font-size: 20px;
  margin-bottom: 20px;
  color: var(--text-dark);
  position: relative;
  padding-bottom: 10px;
}

.book-detail-section h3::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 50px;
  height: 3px;
  background-color: var(--primary-color);
}

.book-detail-section p {
  font-size: 16px;
  line-height: 1.7;
  color: var(--text-dark);
}

.book-details-list {
  list-style: none;
  padding: 0;
}

.book-details-list li {
  margin-bottom: 12px;
  font-size: 16px;
  color: var(--text-dark);
}

/* Estilo para wishlist badge na página de detalhes */
.detail-badge {
  top: 20px;
  left: 20px;
  width: 36px;
  height: 36px;
  z-index: 10;
}

/* Estilo do ripple effect para os botões */
.action-button {
  position: relative;
  overflow: hidden;
}

.action-button::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 5px;
  height: 5px;
  background: rgba(255, 255, 255, 0.5);
  opacity: 0;
  border-radius: 100%;
  transform: scale(1, 1) translate(-50%);
  transform-origin: 50% 50%;
}

.action-button:focus:not(:active)::after {
  animation: ripple 1s ease-out;
}

@keyframes ripple {
  0% {
    transform: scale(0, 0);
    opacity: 0.5;
  }
  20% {
    transform: scale(25, 25);
    opacity: 0.3;
  }
  100% {
    opacity: 0;
    transform: scale(40, 40);
  }
}

@media (max-width: 900px) {
  .book-detail-container {
    flex-direction: column;
  }
  
  .book-detail-cover {
    flex: 0 0 auto;
    height: 400px;
    max-width: 280px;
    margin: 0 auto 30px;
  }
  
  .book-detail-sections {
    grid-template-columns: 1fr;
  }
} 