.catalog-page {
  padding: 40px 20px;
  max-width: 1400px;
  margin: 0 auto;
  min-height: 80vh;
}

.catalog-header {
  text-align: center;
  margin-bottom: 40px;
}

.catalog-header h1 {
  font-size: 42px;
  color: var(--text-dark);
  margin-bottom: 15px;
  font-weight: 700;
}

.catalog-header p {
  font-size: 18px;
  color: var(--text-muted);
  max-width: 700px;
  margin: 0 auto;
}

.catalog-filters {
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.genre-filter h3 {
  font-size: 18px;
  margin-bottom: 15px;
  color: var(--text-dark);
  font-weight: 600;
}

.genre-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.genre-buttons button {
  background-color: #f5f7fa;
  border: 1px solid #e1e5eb;
  border-radius: 20px;
  padding: 8px 16px;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.3s ease;
  color: var(--text-dark);
}

.genre-buttons button:hover {
  background-color: #e9ecf2;
  transform: translateY(-2px);
}

.genre-buttons button.active {
  background-color: var(--primary-color);
  color: white;
  border-color: var(--primary-color);
  box-shadow: 0 4px 10px rgba(52, 152, 219, 0.3);
}

.search-results {
  padding: 15px;
  background-color: rgba(52, 152, 219, 0.1);
  border-radius: 8px;
  border-left: 4px solid var(--primary-color);
}

.search-results p {
  font-size: 15px;
  color: var(--text-dark);
}

.catalog-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 30px;
  margin-top: 20px;
}

.no-results {
  grid-column: 1 / -1;
  text-align: center;
  padding: 60px 20px;
  background-color: #f9fafc;
  border-radius: 10px;
  border: 1px dashed #d1d9e6;
}

.no-results h3 {
  font-size: 24px;
  color: var(--text-dark);
  margin-bottom: 15px;
}

.no-results p {
  font-size: 16px;
  color: var(--text-muted);
  max-width: 400px;
  margin: 0 auto;
}

/* Responsividade */
@media (max-width: 768px) {
  .catalog-page {
    padding: 30px 15px;
  }
  
  .catalog-header h1 {
    font-size: 32px;
  }
  
  .catalog-grid {
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
    gap: 20px;
  }
} 