.search-bar {
  position: relative;
}

.search-bar form {
  display: flex;
  align-items: center;
}

.search-bar input {
  padding: 10px 15px;
  width: 220px;
  font-size: 14px;
  border: none;
  border-radius: 25px;
  background-color: rgba(255, 255, 255, 0.12);
  color: white;
  outline: none;
  transition: all 0.3s ease;
}

.search-bar input::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

.search-bar input:focus {
  background-color: rgba(255, 255, 255, 0.2);
  width: 250px;
  box-shadow: 0 0 10px rgba(0, 123, 255, 0.2);
}

.search-bar button {
  position: absolute;
  right: 12px;
  background: none;
  border: none;
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
  padding: 0;
  transition: all 0.3s ease;
}

.search-bar button:hover {
  color: white;
  transform: scale(1.1);
}

@media (max-width: 768px) {
  .search-bar {
    width: 100%;
    margin-top: 10px;
  }
  
  .search-bar input, 
  .search-bar input:focus {
    width: 100%;
  }
} 